@import './var.scss';

.ant-divider {
  background-color: #fff;
  opacity: 0.08;
}

.home-content {
  @include small-desktop-only {
    font-size: calc(16vw / 1920 * 100);
  }
}

#root .home-content {
  .ant-select {
    @include small-desktop-only {
      font-size: calc(16vw / 1920 * 100);
    }
  }
}

.hero-bg {
  // background: url(../images/home-waves.svg);
  // background-repeat: no-repeat;
  // background-position: 0px -70px;
  // overflow-x: hidden;
  // margin-top: 90px;
  // @media (max-width: $mobile-width-breakpoint) {
  //   margin-top: 18px;
  // }

  .hero {
    @extend %flex-center;
    justify-content: space-between;
    margin: 90px auto 65px;
    max-width: calc(30px + 1200px);
    padding: 15px;

    @include mobile-only {
      flex-direction: column;
      margin: 38px 0 40px;
      padding: 0 15px;
    }

    @include small-desktop-only {
      margin: 5.625em auto 4.0625em;
      padding: 0.9375em 15px;
      max-width: calc(30px + 75em);
    }

    .left-content {
      max-width: 465px;

      @include small-desktop-only {
        max-width: 29em;
      }
    }

    .hero-intro-card {
      .title {
        @extend %gradient-title;
        color: #fff;
        font-weight: 900;
        font-size: 60px;
        line-height: 72px;

        @include mobile-only {
          text-align: center;
          font-weight: 900;
          font-size: 32px;
          line-height: 38px;
        }

        @include small-desktop-only {
          font-size: 3.75em;
          line-height: 120%;
        }
      }

      .content {
        @extend %dim-content;
        text-align: left;
        margin: 14px auto 0 0;
        font-weight: 400;
        font-size: 18px;
        line-height: 26px;

        @include mobile-only {
          text-align: center;
          margin: 4px auto 0 auto;
          font-weight: 400;
          font-size: 14px;
          line-height: 146%;
        }

        @include small-desktop-only {
          font-size: 1.125em;
          line-height: 144.4444%;
          margin: 0.7778em auto 0 0;
        }
      }
    }

    .usdd-link {
      max-width: 90%;
      margin: 13px 24px 30px 9px;
    }

    $card-width-pc: 440px;

    $card-width-mobile: 420px;

    %card {
      white-space: nowrap;
      text-align: center;
      border-radius: 30px;
      width: 440px;

      @include mobile-only {
        width: 100%;
        border-radius: 15px;
      }

      @include small-desktop-only {
        width: 27.5em;
      }

      // @media (max-width: 850px) {
      //   width: 93vw;
      // }

      // @media (max-width: 550px) {
      //   width: 90vw;
      // }
      // @media (max-width: 400px) {
      //   width: 86vw;
      // }
    }

    .hero-tvl-card {
      @extend %card;

      position: relative;
      margin-bottom: 15px;
      background: #31313e;
      padding: 40px 30px;
      text-align: start;

      @include mobile-only {
        transform: none;
        padding: 20px;
        margin-bottom: 0;
      }

      @include small-desktop-only {
        padding: 2.5em 1.875em;
        margin-bottom: 0.9375em;
      }

      .bg-tvl-card1 {
        transform: rotate(-2.5deg);
        background: rgba(49, 49, 62, 0.7);
      }

      .bg-tvl-card2 {
        transform: rotate(-5deg);
        background: rgba(49, 49, 62, 0.4);
      }

      .bg-tvl-card1,
      .bg-tvl-card2 {
        @extend %card;

        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        @include mobile-only {
          background: none;
          transform: none;
        }
      }

      .coin-bg {
        position: relative;

        &::after {
          content: ' ';
          position: absolute;
          right: 0;
          background: url(../images/coins-bg.svg);
          background-repeat: no-repeat;
          width: 120px;
          height: 138px;
          background-size: 121px 138px;
          bottom: -10px;

          @include mobile-only {
            right: 10px;
            bottom: 10px;
          }

          @include small-desktop-only {
            width: 6.302vw;
            height: 7.1875vw;
            background-size: 6.302vw 7.1875vw;
            bottom: -0.5208vw;
          }
        }

        .title {
          color: #fff !important;
          opacity: 0.5;
          font-weight: 400;
          font-size: 14px;
          line-height: 17px;

          @include mobile-only {
            font-size: 12px;
            line-height: 14px;
          }

          @media screen and (min-width: ($mobile-width-breakpoint)) and (max-width: (1440px - 1)) {
            font-size: 1.125em;
            line-height: 121.4286%;
          }
        }

        .content {
          margin-top: 11px;
          height: 50px;
          color: #fff !important;
          // text-align: end;
          opacity: 1;
          font-weight: 700;
          font-size: 36px;
          line-height: 43px;

          @include mobile-only {
            font-size: 24px;
            line-height: 29px;
            height: auto;
            margin-bottom: 20px;
          }

          // 1080 - 1439
          @media screen and (min-width: ($mobile-width-breakpoint)) and (max-width: (1440px - 1)) {
            font-size: 2.625em;
            line-height: 119.4444%;
          }

          // 1440 - 1919
          @media screen and (min-width: (1440px)) and (max-width: (1920px - 1)) {
            font-size: 32px;
            line-height: 38.4px;
          }
        }

        .title2 {
          margin-top: 16px;
          color: #fff !important;
          font-weight: 400;
          font-size: 14px;
          line-height: 17px;
          opacity: 0.5;

          @include mobile-only {
            text-align: left;
            font-size: 12px;
            line-height: 14px;
          }

          @media screen and (min-width: ($mobile-width-breakpoint)) and (max-width: (1440px - 1)) {
            font-size: 1.125em;
            line-height: 121.4286%;
          }
        }

        .content2 {
          margin-top: 8px;
          font-weight: 700;
          font-size: 18px;
          line-height: 22px;
          height: 22px;
          color: #fff !important;
          // margin-bottom: 4px;
          opacity: 1;

          @include mobile-only {
            text-align: left;
            font-size: 14px;
            line-height: 17px;
          }

          @media screen and (min-width: ($mobile-width-breakpoint)) and (max-width: (1440px - 1)) {
            font-size: 1.5em;
            line-height: 122.2222%;
          }
        }
      }
    }

    .hero-swap-box-card {
      .box-header {
        font-weight: 700;
        font-size: 40px;
        line-height: 48px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;

        @include mobile-only {
          font-weight: 700;
          font-size: 24px;
          line-height: 29px;
          margin-bottom: 16px;
        }

        @include small-desktop-only {
          font-size: 2.5em;
          line-height: 120%;
          margin-bottom: 0.75em;
        }
      }

      .box-body {
        display: flex;
        flex-direction: column;
        position: relative;

        .swap-box-arrow {
          position: absolute;
          height: 40px;
          width: 40px;
          background-size: cover;
          background-repeat: no-repeat;
          left: 50%;
          top: 50%;
          transform: translateX(-50%) translateY(-50%);
          cursor: pointer;
          background-image: url(../images/swap-box-arrow.svg);

          &:before {
            content: url(../images/swap-box-arrow-purple.svg);
            width: 0;
            height: 0;
            visibility: hidden;
          }

          &:hover {
            background-image: url(../images/swap-box-arrow-purple.svg);
          }
        }

        .input-part {
          background: #3b3d4b;
          border-radius: 15px;
          // margin-top:30px;
          padding: 20px;
          height: 180px;
          border: 1px solid #3b3d4b;

          @include mobile-only {
            height: auto;
            border-radius: 10px;
            padding: 10px;
          }

          @include small-desktop-only {
            padding: 1.25em;
            height: 11.25em;
            border-radius: 12px;
          }

          &.from-input {
            margin-bottom: 10px;

            &:hover {
              background: #31333f;
              border: 1px solid #6a6e7e;
            }
          }

          .info-row {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 30px;

            @include mobile-only {
              margin-bottom: 19px;
              min-height: 25px;
            }

            @include small-desktop-only {
              margin-bottom: 1.875em;
            }

            .from-text {
              font-weight: 700;
              font-size: 28px;
              line-height: 34px;
              display: flex;
              align-items: center;
              color: #ffffff;

              @include mobile-only {
                font-size: 14px;
                line-height: 17px;
              }

              @include small-desktop-only {
                font-size: 1.75em;
                line-height: 121.4286%;
              }
            }

            display: flex;
            flex-direction: row;

            .balance-text {
              font-weight: 400;
              color: rgba(255, 255, 255, 0.5);
            }

            .balance-value {
              font-weight: 400;
              color: #ffffff;
              margin-left: 6px;
            }

            .max-btn {
              font-weight: 500;
              display: flex;
              align-items: center;
              color: #9a8aff;
              margin-left: 5px;
              padding: 4px 8px;
              padding-right: 0px;
              text-transform: uppercase;
              border-radius: 6px;
              cursor: pointer;
            }

            .balance-text,
            .balance-value,
            .max-btn {
              font-size: 16px;
              line-height: 19px;

              @include mobile-only {
                font-size: 14px;
                line-height: 17px;
              }

              @include small-desktop-only {
                font-size: 1em;
                line-height: 118.75%;
              }
            }
          }

          .ant-input {
            height: 53px !important;
            white-space: nowrap;
            overflow: hidden !important;
            text-overflow: ellipsis;
            padding-right: 16px;

            background: transparent !important;
            border: none !important;
            padding-left: 0;
            font-weight: 700;
            font-size: 44px;
            line-height: 53px;
            color: #ffffff !important;

            &::placeholder {
              color: rgba(255, 255, 255, 0.05);
            }

            @include mobile-only {
              font-size: 24px;
              line-height: 29px;

              &.from-input.en {
                &::placeholder {
                  font-size: 18px;
                }
              }
            }

            @include desktop-only {
              &::placeholder {
                font-size: 28px;
                line-height: 53px;
                position: absolute;
                top: 0;
                left: 0;
              }
            }

            @include small-desktop-only {
              font-size: 2.75em;
              line-height: 120.4545%;
              height: 1.2045em !important;

              &::placeholder {
                font-size: 0.6363em;
                line-height: 1.8928em;
              }
            }

            &.to-input {
              cursor: not-allowed;
              caret-color: transparent;
            }
          }

          .ant-select {
            width: 131px !important;

            @include small-desktop-only {
              flex-shrink: 0;
              width: 8.3125em !important;
              min-width: 115px;
            }
          }

          .ant-select-selector {
            background: #343642 !important;
            border-radius: 10px !important;
            font-size: 20px;
            line-height: 24px;
            color: #ffffff;
            width: 131px !important;
            height: 48px !important;

            @include mobile-only {
              width: 115px !important;
              height: 32px !important;
              font-size: 14px;
              line-height: 17px;
            }

            @include small-desktop-only {
              font-size: 1.25em;
              line-height: 120%;
              width: 6.55em !important;
              height: 2.4em !important;
              padding: 0 0.55rem;
              min-width: 115px;
              border-radius: 6px !important;
            }
          }

          .ant-select-selection-search-input {
            font-size: 16px !important;

            @include small-desktop-only {
              font-size: 1.25em;
            }
          }

          .ant-select-single.ant-select-open .ant-select-selection-item {
            color: #ffffff !important;
          }

          .ant-select-selection-item {
            display: flex;
          }

          .token-logo {
            width: 28px !important;
            height: 28px !important;
            margin-right: 8px !important;

            @include mobile-only {
              width: 20px !important;
              height: 20px !important;
            }

            @include small-desktop-only {
              width: 1.4em !important;
              height: 1.4em !important;
              margin-right: 0.3em !important;
            }
          }

          .ant-select-arrow {
            margin-bottom: 20px;
            top: 50%;

            @include small-desktop-only {
              margin-bottom: 1.25em;
            }
          }

          .ant-select-open .ant-select-arrow {
            transform: rotate(180deg);
          }

          &:hover .ant-select-selector {
            background: #2c2e38 !important;
          }

          // &:hover .max-btn:hover {
          //   background:#2C2E38 !important;
          // }
        }
      }

      .box-footer {
        .footer-info-row {
          display: flex;
          justify-content: space-between;
          margin-top: 16px;
          margin-bottom: 30px;
          // font-weight: 700;
          font-size: 18px;
          line-height: 22px;

          @include mobile-only {
            font-weight: 700;
            font-size: 14px;
            line-height: 17px;
            margin-top: 12px;
            margin-bottom: 20px;
          }

          @include small-desktop-only {
            font-size: 1.125em;
            line-height: 122.2222%;
            margin-top: 0.8889em;
            margin-bottom: 1.6667em;
          }

          .swap-rate {
            display: flex;
            justify-content: space-between;
            align-items: center;
          }

          .purple-switch {
            margin-left: 8px;
            cursor: pointer;
            height: 24px;
            width: 24px;
          }
        }

        .via-pool {
          .via {
            color: #a7a8ad;
          }
        }

        .submit-btn {
          @extend %btn;
          background: linear-gradient(79.72deg, #4504fe 4.86%, #e64dff 103.07%);
          font-style: normal;
          display: flex;
          align-items: center;
          border-radius: 15px;
          font-weight: 700;
          font-size: 20px;
          line-height: 24px;
          width: 100%;

          &.disabled {
            color: rgba(255, 255, 255, 0.4);
            cursor: not-allowed;
          }

          &:hover {
            background: linear-gradient(79.72deg, #4504fee0 4.86%, #e64dffe0 103.07%);
          }

          @include mobile-only {
            font-size: 14px;
            line-height: 17px;
          }

          @include small-desktop-only {
            font-size: 1.25em;
            line-height: 120%;
            height: 3.3333em;
            border-radius: 12px;
          }
        }
      }
    }
  }
}

.apy-section {
  @extend %layout;
  @extend %blur-bg;

  @include desktop-only {
    border-radius: 30px;
    padding: 60px 24px;
  }

  @include mobile-only {
    background: transparent;
    backdrop-filter: initial;
    padding: 0;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  @include small-desktop-only {
    padding: 3.75em 1.5em;
    margin: 2.5em auto;
    border-radius: 24px;
  }

  .slogan {
    // margin-top: 16px;
    .title {
      @extend %gradient-title;
      color: #fff;
      font-weight: 600;
      font-size: 32px;
      line-height: 45px;
      text-align: center;
      max-width: 833px;
      margin: 0 auto;

      @include mobile-only {
        font-weight: 600;
        font-size: 18px;
        line-height: 25px;
      }

      @include small-desktop-only {
        max-width: 43.3854vw;
        font-size: 2em;
        line-height: 140.625%;
      }
    }

    .content {
      @extend %dim-content;
      margin: 0 auto;
      margin-top: 8px;
      margin-bottom: 60px;
      text-align: center;
      line-height: 32px;

      &.en {
        max-width: 689px;

        @include small-desktop-only {
          max-width: 35.8854vw;
        }

        @include mobile-only {
          max-width: 455px;
        }
      }

      @include mobile-only {
        margin-bottom: 24px;
        font-weight: 400;
        font-size: 12px;
        line-height: 18px;
      }

      @include small-desktop-only {
        font-size: 1.125em;
        line-height: 200%;
        margin-top: 0.4444em;
        margin-bottom: 3.3333em;
      }
    }
  }

  .gradient-border-wrapper {
    display: flex;
    justify-content: space-around;

    @include mobile-only {
      flex-direction: column;
    }

    .gradient-border {
      overflow: hidden;
      &.hot {
        position: relative;
        &::after {
          content: '';
          display: block;
          width: 62.5px;
          height: 27.5px;
          background: url(../images/apy_hot.svg) 0 0 no-repeat;
          background-size: 100%;
          position: absolute;
          top: 0;
          right: 0;
          z-index: 1;
        }
      }
      &:not(:last-child) {
        @include desktop-only {
          margin-right: 20px;
        }

        @include mobile-only {
          margin-bottom: 20px;
        }
      }
    }

    .version-corner {
      background: rgba(255, 189, 174, 0.27);
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      /* identical to box height */

      display: flex;
      align-items: center;
      text-align: center;

      color: #ff8c38;
      position: absolute;
      right: 0;
      top: 0;
      padding: 3px 13px 5px 14px;
      border-top-right-radius: 20px;
      border-bottom-left-radius: 20px;

      &.version1 {
        background: rgba(39, 224, 180, 0.16);
        color: #27e0b4;
      }

      @include mobile-only {
        border-top-right-radius: 15px;
        border-bottom-left-radius: 15px;
      }

      @include small-desktop-only {
        font-size: 1.125em;
        line-height: 122.2222%;
        border-top-right-radius: 15px;
        border-bottom-left-radius: 15px;
      }
    }

    .apy-upper-part {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      @include mobile-only {
        flex-direction: row;
        justify-content: flex-start;
      }

      .token-icons {
        margin: 10px auto 10px;

        img {
          // width: 32px;
          height: 32px;
        }

        @include mobile-only {
          margin: 0 12px 0 0;
        }

        @include small-desktop-only {
          margin: 0.625em auto 0.625em;
        }
      }

      .symbol-text {
        font-size: 14px;
        line-height: 17px;
        color: #ffffff;
        height: 17px;
        font-weight: 600;

        @include small-desktop-only {
          font-size: 0.875em;
          line-height: 121.4286%;
        }
      }
    }

    .ant-divider-horizontal {
      margin: 16px auto;

      @include small-desktop-only {
        font-size: 1em;
        margin: 1em;
      }
    }

    .apy-lower-part {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      @include mobile-only {
        flex-direction: row;
        justify-content: space-between;
      }

      @media (max-width: 450px) {
        flex-direction: row;
        justify-content: space-between;
      }

      .apy-up-to-text {
        text-align: center;

        @include mobile-only {
          display: flex;
          align-items: center;
          flex-direction: row;
        }

        @media (max-width: 450px) {
          flex-direction: row;
          justify-content: space-between;
          margin-right: 10px;
        }

        .apy-up-to {
          color: #fff;
          opacity: 0.4;
          font-weight: 400;
          font-size: 12px;
          line-height: 14px;
          align-self: center;
          margin: auto;

          @include mobile-only {
            align-self: center;
            margin-right: 4px;
          }

          @include small-desktop-only {
            font-size: 0.75em;
            line-height: 116.6667%;
          }
        }

        .apy-text {
          margin-top: 8px;
          display: flex;
          align-items: center;
          text-align: center;

          color: #ffffff;
          height: 34px;
          font-weight: bold;
          font-size: 24px;
          line-height: 29px;

          @include mobile-only {
            margin-top: 0px;
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
          }

          @include small-desktop-only {
            margin-top: 0.3333em;
            font-size: 1.5em;
            line-height: 120.8333%;
          }
        }
      }

      .apy-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: 'Lato';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        background: linear-gradient(79.72deg, #4504fe 4.86%, #e64dff 103.07%);
        border-radius: 6px;
        margin: 26px auto 10px;
        width: 100%;
        color: #fff;

        padding: 12px auto;
        height: 40px;
        border-radius: 10px;

        &:hover {
          background: linear-gradient(79.72deg, #4504fee0 4.86%, #e64dffe0 103.07%);
        }

        @include mobile-only {
          color: #ffffff !important;
          // width: clamp(94px, 168px, 50%);
          // height: clamp(28px, 40px, 50%);
          width: 120px;
          height: 28px;
          margin: 0;
          padding: 7px 12px;
          color: #ffffff;
          border-radius: 6px;
        }

        @include small-desktop-only {
          font-size: 0.875em;
          line-height: 121.4286%;
          margin: 1.625em auto 0.625em;
          height: 2.8571em;
          border-radius: 8px;
        }
      }
    }
  }
}

.ant-select-dropdown {
  background: #2c2e38 !important;
  padding: 0;
  border-radius: 10px;

  @include small-desktop-only {
    border-radius: 6px;
  }

  .ant-select-item {
    border-radius: 8px;
    margin: 2px;
    padding: 5px 10px;

    img {
      width: 28px;
      height: 28px;
    }

    // font-weight: bold;
    font-size: 20px;
    line-height: 24px;
    color: #ffffff;
    background: #2c2e38 !important;

    @include small-desktop-only {
      font-size: calc(20vw / 1920 * 100);
      line-height: 120%;
      margin: 0.1em;
      padding: 0.25em 0.5em;
    }

    .ant-select-item-option-content {
      display: flex;
      align-items: center;
      width: 100%;
      padding: 4px 0;

      img {
        margin-right: 8px;
      }

      @include mobile-only {
        font-size: 14px;
        line-height: 24px;

        img {
          width: 20px;
        }
      }

      @include small-desktop-only {
