          height: 1.4em;
          width: 1.4em;
          margin-right: 0.3em;
        }
      }
    }

    &:hover {
      background: #5915e1 !important;
      color: #ffffff !important;
    }

    &.ant-select-item-option-selected {
      color: #ffffff !important;
    }
  }
}

