@property --angle {
  syntax: '<angle>';
  initial-value: 0deg;
  inherits: false;
}

%gradient-border {
  width: clamp(100px, 212px, 90%);
  text-align: center;
  overflow-wrap: break-word;
  display: inline-block;
  margin: 0px;
  padding: 22px;
  position: relative;
  z-index: 0;
  color: #fff;
  background: #393851;
  border-radius: 20px;

  @include mobile-only {
    width: 100%;
    border-radius: 15px;
  }

  @include small-desktop-only {
    padding: 1.375em;
    border-radius: 15px;
  }
}

.gradient-border2 {
  @extend %gradient-border;
  background: #21222f;
  // box-shadow: 0px 4px 26px 1px rgba(54, 206, 254, 0.1);
  padding: 34px;
  margin-bottom: 15px;

  @include desktop-only {
    width: min(630px, 90vw);
  }

  @include mobile-only {
    width: 100%;
    border-radius: 15px;
    padding: 24px 19px;
  }

  @include small-desktop-only {
    padding: 2.125em;
    margin-bottom: 0.9375em;
    width: min(39.375em, 90vw);
  }
}

.gradient-border {
  @extend %gradient-border;

  &:hover {
    background: #2f2f45;
  }
}

%gradient-border-before {
  @keyframes rotate {
    from {
      --angle: 0deg;
    }

    to {
      --angle: 360deg;
    }
  }

  content: '';
  position: absolute;
  z-index: 2;
  inset: 0;
  padding: 1px;
  border-radius: 20px;
  background: linear-gradient(135deg, #66ffed, #bf5aee, #67a4ff, #ff9141);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;

  /* Check for Houdini support & register property */
  @supports (background: paint(something)) {
    background: linear-gradient(var(--angle), #66ffed, #bf5aee, #67a4ff, #ff9141);
    animation: 5s rotate linear infinite;
  }

  @include mobile-only {
    // border-radius: 15px;
  }

  @include small-desktop-only {
    // border-radius: 15px;
  }
}

.gradient-border:before {
  @extend %gradient-border-before;
}

.gradient-border2:before {
  @extend %gradient-border-before;
  border-radius: 30px;
  padding: 4px;

  @include mobile-only {
    border-radius: 15px;
  }

  @include small-desktop-only {
    border-radius: 24px;
  }
}

.gradient-border:hover .apy-btn {
  color: #ffffff !important;
}

.ant-row {
  flex-wrap: nowrap;

  .ant-col {
    &:not(:last-child) {
      margin-right: 6px;
    }
  }
}

@media screen and (max-width: 1920px) {
  .apy-section .gradient-border-wrapper .gradient-border {
    &::before {
      border-radius: 20px !important;
    }
  }
  .gradient-border {
    border-radius: 20px !important;
    overflow: hidden;
  }
  .apy-section .gradient-border-wrapper .version-corner {
    border-top-right-radius: 20px !important;
    border-bottom-left-radius: 20px !important;
  }
}

@media screen and (max-width: 1500px) {
  .apy-section .gradient-border-wrapper .gradient-border.hot::after {
    width: 38%;
  }
}
@media screen and (max-width: 1080px) {
  .apy-section .gradient-border-wrapper .gradient-border.hot::after {
    width: 62.5px;
  }
}
